body {
  background-color: #009DDC;
  color: #2A2D34; }

.dark {
  color: #2A2D34; }

.btn {
  margin-bottom: 0.5rem; }

.card {
  background-color: #5e5e61;
  color: #ffffff;
  margin-bottom: 1rem;
  position: relative; }

.show-code {
  background-color: #fefefe; }
  .show-code svg {
    color: #5e5e61; }

header {
  margin: 1rem 0; }

h1 {
  font-size: 1.8rem; }

code {
  background-color: #fefefe;
  padding: 0.5rem;
  display: block;
  margin-bottom: 0.5rem;
  overflow: scroll; }

.github-link {
  position: fixed;
  bottom: 0.5rem;
  right: 0.5rem;
  height: 3rem;
  width: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fefefe;
  border-radius: 50%;
  box-shadow: -2px 3px 5px black; }

@media screen and (min-width: 768px) {
  h1 {
    font-size: 2.4rem; } }
