.fold {
  display: block;
  max-height: 0px;
  overflow: hidden;
  transition: max-height 0.5s cubic-bezier(0, 1, 0, 1); }
  .fold.show {
    max-height: 99em;
    transition: max-height 0.5s ease-in-out; }

.custom-nav {
  background-color: #2A2D34;
  color: #fefefe; }
  .custom-nav__toggle svg {
    color: #fefefe; }
  .custom-nav__code, .custom-nav__code.fold {
    text-align: left;
    display: flex;
    align-content: center;
    justify-content: center; }
  .custom-nav__body {
    position: relative;
    text-align: center; }
    .custom-nav__body figure {
      text-align: center;
      position: relative;
      width: fit-content;
      display: inline-block; }
      .custom-nav__body figure .code-toggle-btn {
        background: #fefefe;
        border: 3px solid #009DDC; }
        .custom-nav__body figure .code-toggle-btn svg {
          color: #2A2D34; }
    .custom-nav__body img {
      max-width: 100%; }
  .custom-nav .btn {
    margin-bottom: 0; }
